// _projects.scss

// 標籤雲樣式
.tag-cloud {
    margin-bottom: 30px;
    text-align: center;
    
    @media (max-width: 768px) {
      overflow-x: auto;
      white-space: nowrap;
      -webkit-overflow-scrolling: touch;
      scrollbar-width: none;
      -ms-overflow-style: none;
      
      &::-webkit-scrollbar {
        display: none;
      }
    }
  
    .tag {
      display: inline-block;
      margin: 0 -5px; // 縮小了標籤之間的距離
      padding: 5px 10px;
      color: var(--text-color);
      font-size: 0.9em;
      cursor: pointer;
      transition: all var(--transition-duration) ease;
      position: relative;
  
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: var(--text-color);
        transform: scaleX(0);
        transition: transform var(--transition-duration) ease;
      }
  
      &:hover::after,
      &.active::after {
        transform: scaleX(1);
      }
    }
  }
  
  // 作品容器樣式
  .project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }
  
  // 作品鏈接樣式
  .project-link {
    @include no-underline;
  }
  
  // 作品項目樣式
  .project-item {
    display: inline-block;
    margin: 5px;
    padding: 5px 10px;
    background-color: rgba(69, 66, 74, 0.6);
    border-radius: var(--border-radius);
    transition: all var(--transition-duration) ease;
    cursor: pointer;
  
    &:hover {
      background-color: rgba(245, 240, 229, 0.2);
  
      .project-title {
        color: var(--white);
      }
    }
  
    .project-title {
      color: var(--text-color);
      font-weight: 500;
      font-family: var(--font-mono);
    }
  }
  
// 響應式設計
@media (max-width: 768px) {
    .tag-cloud {
      padding: 10px 0;
      
      .tag {
        font-size: 0.8em;
        // margin: 0 -3px;
        padding: 5px 8px;
      }
    }
  
    .project-item {
      font-size: 0.9em;
    }
  }
  
  @media (max-width: 480px) {
    .project-container {
      padding: 0 10px;
    }
  }